html,body{
  height: 100%;
}
#root{
  height: 100%;;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
header{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
height: 100%;
}
h1{
font-size: 50px;
font-weight: bold;
}
p{
font-size: 20px;
line-height: 1.6;
display: flex;
justify-content: center;
font-size: 16px;
width:100%;
margin: 0;
height: 29px;
}
footer{
  display: flex;
  justify-content: center;
  background-color: #eee;
}
.na{
  color: blue;
  font-size: 18px;
}
img{
  height: 60%;
}